import React, { useState } from "react";
import { Modal, Button, Space } from "antd";

function About(props) {
  const [ismodalopen, setIsModalOpen] = useState(false);
  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {
    setIsModalOpen(false);
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <Space>
      <Button onClick={showModal}>About</Button>
      <Modal visible={ismodalopen} onOk={handleOk} onCancel={handleCancel}>
        <p>这是一个兴趣使然的小项目</p>
        <p>
          最初写这个小工具是因为我的一位朋友用的mac,没办法使用原来老的默写器,当时我刚刚开始学前端开发,就想着写一个网页版。难度比我想的略大一点,现在摆在你眼前的是第三版。基本实现了我预期的大部分功能。
        </p>
        <p>或许我不能称为灿若星辰的浙大人,但就这么做一些能帮助到别人的事情也是挺有意义的。</p>
        <p>如果你有什么建议或者意见,欢迎联系我。98搜ADSR 站短即可</p>
        <p>感谢ZJUEVA 提供服务器支持</p>
        祝愿各位 大英满绩
        <p></p>
        <div>
          <em>武霁澜</em>
        </div>
      </Modal>
    </Space>
  );
}
export default About;
